/*OFFCANVAS*/
#off-canvas-toggle {
	border-right: 1px solid;
	display: inline-block;
	height: 38px;
	margin-right: 20px;
	min-width: 50px;
	padding: 0 20px 0 0;
}
#off-canvas-toggle span {
	background: #000 none repeat scroll 0 0;
	display: inherit;
	height: 2px;
	position: relative;
	width: 25px;
}
#off-canvas-toggle span::before,
#off-canvas-toggle span::after {
	background: #000 none repeat scroll 0 0;
	content: "";
	height: 2px;
	left: 0;
	position: absolute;
	right: 0;
}
#off-canvas-toggle span::after {
	top: -5px;
	width: 20px;
}
#off-canvas-toggle span::before {
	top: -10px;
	width: 15px;
}
#off-canvas-toggle:hover span::before {
	width: 25px;
}
#off-canvas-toggle:hover span {
	width: 15px;
	margin-right: 10px;
}
#off-canvas-toggle p {
	margin-left: 10px;
	display: inline;
}
